<template>
    <div class="container card text-center" align="center">
        <div class="row text-center pt-4" align="center">
            <b-tabs card class="col-lg-12">
                <b-tab v-for="i in tabs" :key="'dyn-tab-' + i" :title="i">
                    <div>
                        <div class="container">
                            <div class="row">
                                <div class="col-sm">
                                    <div>
                                        <b-table
                                            selectable
                                            selectedVariant="success"
                                            select-mode="single"
                                            responsive="sm"
                                            striped hover :small="true" 
                                            :items="items"
                                            @row-dblclicked="rowSelected"
                                            ></b-table>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <b-button size="sm" variant="danger" class="float-right" @click="closeTab(i)">
                      Close tab
                    </b-button>
                </b-tab>

                <!-- New Tab Button (Using tabs slot) -->
                <template slot="tabs">
                <b-nav-item @click.prevent="newTab" href="#"><b>+</b></b-nav-item>
                </template>

                <!-- Render this if no tabs -->
                <div slot="empty" class="text-center text-muted">
                There are no open tabs<br>
                Open a new tab using the <b>+</b> button above.
                </div>
            </b-tabs>
        </div>
    </div>
    
</template>
<script>
// import detalle from '../components/CostoPromedio/DetalleCategorias';
const axios = require('axios');
const moment = require("moment")
var _ = require('lodash');
export default {
  props: ['selectedCategory', 'fechaFin', 'fechaInicio'],
  components: {
    // detalle
  },
  data () {
    return {
      items: [],
      selected: [],
      tabs: [this.selectedCategory.nombre],
      totalGastado: 0,
      totalCobrado: 0,
      comensales: 0
    }
  },
  mounted () {
      // eslint-disable-next-line no-console
      console.log('entro a traer todo', this.selectedCategory.nombre)
      switch (this.selectedCategory.nombre) {
        case "Catidad de eventos":
          this.numeroEventos();
          break;
        case "Catidad de comensales":
          this.numeroComensales();
          break;
        case "Eventos adicionales":
          this.numeroEventosAdicionales();
          break;
        case "Eventos recaudación":
          this.numeroEventosRecaudacion();
          break;
        default:
          break;
      }
  },
  methods: {
    async getAllFunctions () {
      // eslint-disable-next-line no-console
      console.log('entro a traer todo')
      this.items = []
      await this.numeroEventos()
      await this.numeroComensales()
      await this.cobrado()
      await this.numeroEventosAdicionales()
      await this.numeroEventosRecaudacion()
      await this.ventaVinos()
      await this.ventas()
      await this.gatoInsumo()
      await this.gatoActivo()
      await this.gastoOperativo()
      await this.nomina()
      await this.premioDeLealtad()
      await this.gastado()
      await this.gastoPersonal()
      await this.costoPromedio()
      await this.precioPromedio()
      await this.utilidad()
    },
    async numeroEventos () {
      await this.getData('numeroEventosDetalle').then((data) => {
        this.items = data
      })
    },
    async numeroComensales () {
      await this.getData('numeroComensales').then((data) => {
          this.items.push({tipo: 'Contratos', cantidad: data.total_contrato});
          this.items.push({tipo: 'Cargos ', cantidad: data.total_cargos});
          this.items.push({tipo: 'Cargos facturados', cantidad: data.total_facturados});
      })
    },
    async cobrado () {
      await this.getData('totalCobrado').then((data) => {
        this.data.push({nombre: 'Total cobrado', total: _.floor(data, 4)});
        this.totalCobrado += parseFloat(data)
      })
    },
    async numeroEventosAdicionales () {
      await this.getData('eventosAdicionalesDetalle').then((data) => {
        _.forEach(data, (val) => {
          this.items.push({numero: val.numero, fecha: val.fecha, tipo: val.tipo, salon:val.salon})
        })
      })
    },
    async numeroEventosRecaudacion () {
      await this.getData('eventosRecaudacionDetalle').then((data) => {
        _.forEach(data, (val) => {
          this.items.push({numero: val.numero, fecha: val.fecha, referencia: val.referencia, salon:val.salon})
        })
      })
    },
    async ventaVinos () {
      await this.getData('ventaVinosDetalle').then((data) => {
        this.data.push({nombre: 'Venta de vinos', total: _.floor(data, 4)});
      })
    },
    async ventas () {
      await this.getData('ventas').then((data) => {
        this.data.push({nombre: 'Ventas', total: _.floor(data, 4)});
      })
    },
    async cancelaciones () {
      await this.getData('cancelaciones').then((data) => {
        this.data.push({nombre: 'Cancelaciones', total: _.floor(data, 4)});
      })
    },
    async gatoInsumo () {
      await this.getData('gastoInsumo').then((data) => {
        this.data.push({nombre: 'Gasto insumo', total: _.floor(data, 4)});
        this.totalGastado += parseFloat(data);
      })
    },
    async gatoActivo () {
      await this.getData('gastoActivo').then((data) => {
        this.data.push({nombre: 'Gasto activo', total: _.floor(data, 4)});
        this.totalGastado += parseFloat(data);

      })
    },
    async gastoOperativo () {
      await this.getData('gastoOperativo').then((data) => {
        this.data.push({nombre: 'Gasto operativo', total: _.floor(data, 4)});
        this.totalGastado += parseFloat(data);
      })
    },
    async nomina () {
      await this.getData('nomina').then((data) => {
        this.data.push({nombre: 'Nomina', total: _.floor(data, 4)});
        this.totalGastado += parseFloat(data);
      })
    },
    async premioDeLealtad () {
      await this.getData('premioDeLealtad').then((data) => {
        this.data.push({nombre: 'Premio de lealtad', total: _.floor(data, 4)});
        this.totalGastado += parseFloat(data);
      })
    },
    gastado () { // total gastado
    this.data2.push({nombre: 'Total gastado', total: _.floor(this.totalGastado, 4)});
    },
    async gastoPersonal () {
      await this.getData('gastoPersonal').then((data) => {
        this.data.push({nombre: 'Gasto personal', total: _.floor(data, 4)});
      })
    },
    async costoPromedio () {
      this.data2.push({nombre: 'Costo promedio', total: _.floor((this.totalGastado/this.comensales), 4)});
    },
    async precioPromedio () {
      this.data2.push({nombre: 'Precio promedio', total: _.floor((this.totalCobrado/this.comensales), 4)});
    },
    async utilidad () {
      this.data2.push({nombre: 'Utilidad', total: _.floor((this.totalCobrado - this.totalGastado), 4)});
    },
    async getData (opcion) {
      return await new Promise((resolve, reject) => {
        try {
          let payload = {
            fecha1: moment(this.fechaInicio).format('YYYY-MM-DD'),
            fecha2: moment(this.fechaFin).format('YYYY-MM-DD'),
            opcion: opcion
          }
          let personForm = this.toFormData(payload);
          axios
            .post('https://villaconin.mx/reportes/costoPromedio.php', personForm)
            .then(response => {
                // eslint-disable-next-line no-console
                console.log(response)
                if (_.isEmpty(response)) {
                  resolve(0)
                } else {
                  resolve(response.data)
                }
            })
            .catch(error => {
              this.errored = true
              // eslint-disable-next-line no-console
              console.log(error)
            })
            .finally(() => this.loading = false)
        }
        catch (error) {
          reject(error)
        }
      })
    },
    toFormData(obj) {
      let formData = new FormData();
      for(let key in obj) {
        formData.append(key, obj[key]);
      }
      return formData;
    },
    formatPrice(value) {
      let val = (value/1).toFixed(2)
      return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
    },
    rowSelected(items) {
      this.selected = items;
      this.tabs.push(String(items.nombre));
    },
    closeTab(x) {
      for (let i = 0; i < this.tabs.length; i++) {
        if (this.tabs[i] === x) {
          this.tabs.splice(i, 1)
        }
      }
    }
  },
  created () {
      // eslint-disable-next-line no-console
      console.log('selectedCategory', this.selectedCategory)
  }
}
</script>
